import Link from "next/link";
import cases from "@/data/mock_cases.json";
import { CaseSchema } from "@/lib/schema";

import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";

function fmtTs(iso: string) {
  try {
    const d = new Date(iso);
    return d.toISOString().replace("T", " ").replace(".000Z", "Z");
  } catch {
    return iso;
  }
}

function riskTone(label: string) {
  if (label === "BLOCK") return { pill: "bg-red-600 text-white", box: "bg-red-50 border-red-200" };
  if (label === "ESCALATE") return { pill: "bg-orange-500 text-white", box: "bg-orange-50 border-orange-200" };
  return { pill: "bg-slate-800 text-white", box: "bg-slate-50 border-slate-200" };
}

export default async function CaseDetailPage({
  params,
}: {
  params: Promise<{ id: string }>;
}) {
  const { id } = await params;

  const found = (cases as any[]).find((c) => c.id === id);
  if (!found) throw new Error("Case not found");

  const c = CaseSchema.parse(found);
  const tone = riskTone(c.risk_label);

  const title =
    c.tool_name === "Access_Alex_Welfare_Records"
      ? "Urgent Help with Unemployment Claim"
      : `Review: ${c.tool_name}`;

  const agentName = "Agent_Anna"; // MVP: hardcode. Later: from audit log / backend.
  const requestedAt = fmtTs(c.created_at);

  return (
    <div className="min-h-screen bg-muted/40 p-6">
      <div className="mx-auto w-full max-w-3xl space-y-4">
        <div className="flex items-center justify-between">
          <Link href="/cases" className="text-sm underline">
            Back
          </Link>

          <Button size="sm">New Action</Button>
        </div>

        <Card className="shadow-sm">
          <CardHeader className="space-y-3">
            <div className="flex flex-wrap gap-2">
              <Badge className="bg-sky-600 text-white hover:bg-sky-600">SAFETY_GUIDANCE</Badge>
              <Badge className={tone.pill + " hover:" + tone.pill}>{c.risk_label}</Badge>
            </div>

            <div className="space-y-1">
              <div className="text-xl font-semibold">{title}</div>
              <div className="text-sm text-muted-foreground">
                The customer needs assistance with a benefits claim that may be blocked or delayed due to missing evidence.
              </div>
            </div>
          </CardHeader>

          <CardContent className="space-y-5">
            <div className="text-sm space-y-1">
              <div>
                <span className="text-muted-foreground">Requested by:</span> {agentName}
              </div>
              <div>
                <span className="text-muted-foreground">Requested:</span> {requestedAt}
              </div>
            </div>

            <Separator />

            <div className="space-y-2">
              <div className="text-sm font-semibold">Conversation Context:</div>
              <div className="rounded-md border bg-background p-3 text-sm space-y-2">
                <div>
                  <span className="text-muted-foreground">Customer:</span> {c.user_message}
                </div>
                <div>
                  <span className="text-muted-foreground">Agent:</span> I understand how urgent this is. Let me check what is blocking the payment and what steps can resolve it.
                </div>
                <div>
                  <span className="text-muted-foreground">Customer:</span> Yes, but they are taking too long to respond. I need the benefits to cover my bills.
                </div>
                <div>
                  <span className="text-muted-foreground">Agent:</span> Understood. I will see what I can do to help expedite this process safely.
                </div>
              </div>
            </div>

            <div className="space-y-2">
              <div className="text-sm font-semibold">Description:</div>
              <div className="text-sm text-muted-foreground">
                Customer requests urgent assistance. Tool access may reveal payment status, last action taken, and required evidence to proceed.
              </div>
            </div>

            <div className={"rounded-md border p-4 space-y-3 " + tone.box}>
              <div className="flex items-center justify-between">
                <div className="text-sm font-semibold">Risk Assessment</div>
                <Badge className={tone.pill + " hover:" + tone.pill}>
                  {c.risk_label} RISK
                </Badge>
              </div>

              <div className="text-sm">
                <div className="font-semibold">Reason:</div>
                <div className="text-muted-foreground">{c.risk_rationale}</div>
              </div>

              <div className="text-sm">
                <div className="font-semibold">Risk Labels:</div>
                <div className="flex flex-wrap gap-2 mt-1">
                  {(c.policy_refs || []).slice(0, 3).map((p: string) => (
                    <Badge key={p} variant="secondary" className="text-xs">
                      {p}
                    </Badge>
                  ))}
                </div>
              </div>

              <div className="text-sm">
                <div className="font-semibold">Confidence:</div>
                <div className="text-muted-foreground">{Math.min(99, Math.max(1, c.risk_score))}%</div>
              </div>

              <div className="text-sm">
                <div className="font-semibold">Escalation Route:</div>
                <div className="text-muted-foreground">HUMAN REVIEW</div>
              </div>

              <div className="text-sm">
                <div className="font-semibold">Immediate Steps:</div>
                <ul className="list-disc pl-5 text-muted-foreground space-y-1">
                  <li>Review the user message and scope of the requested tool call.</li>
                  <li>Confirm minimum necessary access and redactions.</li>
                  <li>Approve only if policy allows and rationale is sufficient.</li>
                  <li>If unclear, request more info rather than guessing.</li>
                </ul>
              </div>
            </div>

            <div className="space-y-2">
              <div className="text-sm font-semibold">Notes (Optional)</div>
              <Textarea
                placeholder="Add a short note for audit trail. Example: Approved for implementation. Proceed while awaiting employer evidence."
                className="min-h-[90px]"
              />
            </div>

            <div className="grid grid-cols-2 gap-3 pt-2">
              <Button className="w-full bg-green-600 hover:bg-green-600">Approve</Button>
              <Button className="w-full" variant="destructive">
                Reject
              </Button>
            </div>

            <div className="text-xs text-muted-foreground">
              MVP: buttons are UI-only. Next step is wiring them to POST /api/cases/[id]/decision and appending to immutable audit logs.
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
